header{
    background: #15F0E6;
    width: 100%;
    height: 50px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    color: white;
}

.li-nav{
    margin:0 30px
}
a{
    color: inherit;
    text-decoration: none;
}
a:hover{
    text-decoration: none;
    color: white;
    border-bottom: 1px solid red;
    border-width: 2px;
}

header .icon-container{
    display: flex;
    justify-content: flex-end;
}

.icon{
    cursor: pointer;
    margin: 0 20px;
    height: 30px;
    width: 30px;
}

.icon:hover{
    text-decoration: none;
    color: white;
    border-bottom: 1px solid red;
    border-width: 3px;
}

.button-user{
    background-color: #00a2ff;
    border: none;
    color: white;
}

@media (max-width: 800px) {
    header .li-nav{
        display: none;
    }
    header .icon-container{
        display: grid;
        grid-template-columns: repeat(5, max-content);
        
    }
}