.card{
    height: 350px;
    width: 100%;
    background-color: white;
    border-radius: 0;
    display: grid;
    grid-template-columns: 200px 750px;
}
.img-card{
    width: 150px;
    height: 150px;
    margin: 50px 50px;
    display: inline-block;
}
.flexDescription{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 50px;
}
h4{
    font-size: 40px;
    color: black;
    letter-spacing: 1px;
    cursor: pointer;
}
.tags{
    align-self: flex-start;
    position: relative;
    top: 20px;
    cursor: pointer;
    color: #1354e0;
    font-weight: 600;

}
h4:hover, .tags:hover{
    color: red;
}

.card-description{
    margin: 30px 0;
    color: black;
}
.barCards{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 40px;
    left: 200px;
    color: black;
}
.barCards img{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin-right: 20px;
}
.barCards span{
    color: black;
}
.icon-card{
    height: 20px;
    width: 20px;
    margin-left: 20px;
}
.decoupe{
    color: red;
}
.decoupe:hover{
    border-bottom: red solid 1px;
    color: red;
}
