        background-color: white;
        border-radius: 0;
        display: grid;
        grid-template-columns: none;
        grid-template-rows: 1fr 1fr 1fr 1fr;
    }
    .barCards{
        position: relative;
        left: 0;
        padding-top: 20px;
        display: flex;
