.section-blog{
    width: 70%;
    position: relative;
    margin: 200px auto;
    box-shadow: 0px 0px 0px 7px rgba(16, 4, 4, 0.18);
    border-radius: 1px;
}
.selectoinBar{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 44px;
    background-color: #cecece;
}
h3{
    color: black;
    margin: 0 100px;
    padding-top: 5px;
}

.icon-section{
    width: 30px;
    height: 30px;
    color: black;
    margin: 0 10px;
    padding-top: 10px;
    cursor: pointer;
}

.bar{
    margin-left: 600px;
    box-shadow: 0px 0px 1px 1px rgba(16, 4, 4, 0.47);
}
@media (max-width: 1500px){
    .section-blog{
        width: 100%;
        margin: 200px 0;
        box-shadow: 0px 0px 0px 7px rgba(16, 4, 4, 0.18);
        border-radius: 1px;
    }
    .bar{
        margin-left: 300px;
        box-shadow: 0px 0px 1px 1px rgba(16, 4, 4, 0.47);
    }
}
